.contents__add--table {
    color: #45BBB7;
    cursor: pointer;
    border: solid 0.2rem #45BBB7;
    font-weight: bold;
    padding: 0.4rem 4rem;
    border-radius: 0.3rem;
    -webkit-transition: .3s;
    transition: .3s;
}
.contents__add--table:hover {
    background: #45BBB7;
    color: white;
}
